.navbar {
    height: 70px;
        display: flex;
        padding: 20px 36px;
        box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    background: #F55A5A
    }
    
    .world-icon {
        position:absolute;
        width: 24px;
        height: 24px;
        left: 370px;
        top: 39px;
        display: flex;
        }
    
    .travel-title {
    position:absolute;
    width: 109px;
    height: 16px;
    left: 400px;
    top: 26px;
    display: flex;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: px;
    letter-spacing: -0.100rem;
    
    color: #FFFFFF;
        
    }
    
    .card {
        flex-direction: row;
        display: flex;
        padding-top: 40px;
        padding-left: 100px;
        width: 175px;
        font-size: 12px;
    }
    
    .card--image {
        width: 240%;
        border-radius: 9px;
    }
    
    .title {
    position: static; 
    width: 326px;
    height: 40px;
    left: 200px;
    top: 136px;
    float: right;
    
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    
    color: #2B283A;
    }
    
    .location-symbol {
        width: 20px;
        height: 18px;
        
    }
    
    .location {
    width: 40px;
    height: 10px;
    left: 195px;
    top: 119px;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 12px;
    letter-spacing: 0.17em;
    
    color: #2B283A;
    }
    
    .googleMaps {
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: px;
    margin-left:  13.4px;
    /* identical to box height */
    
    text-decoration-line: underline;
    
    color: #918E9B;
    }
    
    .dates {
    width: 319px;
    height: 11px;
    left: 184px;
    top: 183px;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
    font-size: 11px;
    line-height: 12px;
    
    color: #2B283A;
    
    }
    
    .description {
    width: 326px;
    height: 45px;
    left: 184px;
    top: 203px;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    
    
    
    color: #2B283A;
    }
    
    .card-content {
        padding-left: 90px;
    }
